.title{
  text-align: center;
  margin-top: 3%;
  color:rgb(245, 243, 243);
}

@media only screen and (max-width: 1000px){
  .title{
    margin-top: 6%;
    margin-bottom: 6%;
  }
}

.person{
  
  width:90%;
  margin: 1.5% auto;
  display: flex;
}

.person img {
  width: 70px;
  height: 70px;
  border-radius: 50%;
}

.person div{
 
  display: flex;
  flex-direction: column;
  margin-left: 4%;
  justify-content: center;
  
}

.person div p{
  color: rgb(226, 127, 144);
}

.container{
  width:30%;
  margin: 4% auto;
  background:white;
  display: flex;
  flex-direction: column;
  padding: 2% 0%;
  border-radius: 4px;
  -webkit-box-shadow: 0px 0px 7px 0px rgba(50, 50, 50, 0.65);
  -moz-box-shadow:    0px 0px 7px 0px rgba(50, 50, 50, 0.65);
  box-shadow:         0px 0px 7px 0px rgba(50, 50, 50, 0.65);
}

@media only screen and (max-width: 1000px){
  .container{
    width:45%;
  }
}

@media only screen and (max-width: 700px){
  .container{
    width:55%;
  }
}

@media only screen and (max-width: 510px){
  .container{
    width:65%;
  }
}

@media only screen and (max-width: 400px){
  .container{
    width:85%;
  }
}

@media only screen and (max-width: 300px){
  .container{
    width:90%;
  }
}


.container H3{
  /* text-align: center; */
  margin-left:6%;
  margin-bottom: 5%;
}

.container H3 span{
  color:rgb(226, 127, 144);
}

.container button{
  width:90%;
  margin:5% auto 0%;
  padding: 1.5%;
  border: none;
  color: white;
  background: rgb(247, 108, 131);
  border-radius: 4px;
  font-weight: 500;
  font-size: 16px;
  font-family: 'Yusei Magic', sans-serif;
  cursor: pointer;
}

.container button:hover{
  background:skyblue;
}

.name{
  text-align: center;
  color: white;
  font-size: 12px;
}